          <Latex>
            What this means is that $E_1(``a")$ and (some value related to) $E_1(``b")$ is always in all of $E_i(``abc")$. This applies to $E_1(flag)$ as well: If we were to encrypt "pi" (because those are the first two characters of the flag format), we'll find that $E_1(``p")$ will always be in $E_1(flag)$, and so will (some value related to) $E_1(``i")$. On the other hand, if we were to encrypt "pj", we'll find that (some value related to) $E_1(``j")$ is not in $E_1(flag)$. You can see where this is going.
          </Latex>
